import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {RechargeServiceProvider} from "../../providers/recharge-service/recharge-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";


@IonicPage()
@Component({
  selector: 'page-i-recharge02',
  templateUrl: 'i-recharge02.html',
})
export class IRecharge02Page {
  rechargeError02;
  accountNo;
  shortTitle;
  opPrefix;
  mobileNo;
  packageType;
  fullMobileNo;
  fullPack;
  amount;
  showVar = true;
  data;
  rechargeOperators;
  selectedContact;

  //Add for function call validate recharge;Global variable need for passing parameter
  rechargeMobileNo;
  rechargePackage;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public rechargeService: RechargeServiceProvider,
              public tokenService: TokenServiceProvider,
              public cryptoUtil: CryptoParamUtilProvider) {
    this.accountNo = this.navParams.get("accountNo");
    this.rechargeOperators = this.storage.retrieveFromStorage("rechargeOperator");
    this.selectedContact = this.navParams.get("mobileNo");
    if (this.selectedContact == "" || typeof( this.selectedContact) == "undefined") {
    }

    else {
      this.opPrefix = this.selectedContact.substr(0, 3);
      this.mobileNo = this.selectedContact.substr(3, 8);
    }
  }

  RechargeMobile02Action = function (opPrefix, mobileNo, packageType, amount) {
    this.rechargeError02 = "";

    let validateMobileExp = new RegExp("[0-9]{8}$");
    let validateAmountExp = new RegExp("[0-9]{1,3}$");

    let flag = true;
    this.amount = amount;

    if (this.showVar) {

      if (opPrefix == "" || typeof(opPrefix) == "undefined") {
        this.rechargeError02 = "Please Select Operator";
        flag = false;
        return;
      }
      if (mobileNo == "" || typeof(mobileNo) == "undefined") {
        this.rechargeError02 = "Mobile Number can not be blank.";
        flag = false;
        return;
      }
      if (!validateMobileExp.test(this.mobileNo)) {
        this.rechargeError02 = "Invalid Mobile No.";
        flag = false;
        return;
      }

      if (packageType == "" || typeof(packageType) == "undefined") {
        this.rechargeError02 = "Please Select Package";
        flag = false;
        return;
      }
    }

    if (amount == "" || typeof(amount) == "undefined") {
      this.rechargeError02 = "Recharge amount can not be blank.";
      flag = false;
      return;
    }
    if (!validateAmountExp.test(amount)) {
      this.rechargeError02 = "Invalid Recharge amount.";
      flag = false;
      return;
    }

    if (this.showVar) {
      this.rechargeMobileNo = opPrefix + mobileNo;
      this.rechargePackage = packageType;
    }
    else {
      this.rechargeMobileNo = this.fullMobileNo;
      this.rechargePackage = this.fullPack;
    }
    if (this.rechargePackage == "prepaid") {
      if (amount < 10) {
        this.rechargeError02 = "Minimum recharge amount 10 taka for prepaid.";
        flag = false;
        return;
      }
    }

    if (this.rechargePackage == "postpaid") {
      if (amount < 50) {
        this.rechargeError02 = "Minimum recharge amount 50 taka for Postpaid.";
        flag = false;
        return;
      }
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encAmount = this.cryptoUtil.getEncryptVal(this.amount);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.rechargeService.validateRechargeRequest(unMaskToken, appData.userId, appData.appId, encAmount)
        .then(data => {
          this.onValidateTriggerAmountComplete(data);
        });
    }
  };

  onValidateTriggerAmountComplete = function (data) {
    // console.log(data);
    if (data.statusCode == 1) {
      this.navCtrl.push('IRecharge03Page', {
        'accountNo': this.accountNo,
        'mobileNo': this.rechargeMobileNo,
        'packageType': this.rechargePackage,
        'amount': this.amount,
        'shortTitle': this.shortTitle,
        'triggerAmounts': JSON.stringify(data.object.triggerAmounts),
        'twoFactor': JSON.stringify(data.object.twoFactor)
        // 'twoFactor' :true
      })
    }

    else if (data.statusCode == 0) {
      this.rechargeError02 = data.note;
    }

    else {
      this.rechargeError02 = "System Error Contact with Administrator.";
    }
  };

  showFavoriteList = function () {
    this.rechargeError02 = "";
    let dbVal = this.storage.retrieveFromStorage("appData");
    let unMaskToken = this.maskTokenUtil.getUnMaskVal(dbVal.token);
    this.rechargeService.fetchFavoriteList(unMaskToken, dbVal.userId, dbVal.appId)
      .then(data => {
        // console.log(JSON.stringify(data));
        this.onFetchFavoriteListComplete(data);
        // console.log(this.tokenResponse);
      });
  };

  onFetchFavoriteListComplete = function (data) {

    if (data.statusCode == 404) {
      this.navCtrl.push('ExpirePage');
    }

    else if (data.statusCode == 1) {

      this.data = data;
      let radioAlert = this.alertCtrl.create();
      radioAlert.setTitle('Select Mobile No');
      radioAlert.setCssClass("alertLogCss");

      Object.keys(data.object).forEach(key => {
        let value = data.object[key];
        radioAlert.addInput({
          type: 'radio',
          label: value.phoneNo + ' - ' + value.shortTitle,
          value: value,
          checked: false
        })
      });

      radioAlert.addButton('Cancel');
      radioAlert.addButton({
        text: 'OK',
        handler: data => {
          if (typeof(data) == "undefined") {

          }

          else {
            this.fullMobileNo = data.phoneNo;
            this.fullPack = data.packageType;
            this.showVar = false;
            this.shortTitle = data.shortTitle;
          }
        }
      });
      radioAlert.present();
      setTimeout(() => {
        radioAlert.dismiss();
      }, 40000);
    }


    else if (data.statusCode == 0) {
      this.rechargeError02 = data.note;
      return;
    }

    else {
      this.rechargeError02 = "System Error, Please Contact with Administrator.";
    }
  };


  gotToContactListPage() {
    this.navCtrl.push('ContactListPage', {'accountNo': this.accountNo});
  };


  ionViewDidLoad() {

  }

}



